.container{
    float: left;
    height: 100%;
    width: 100%;
    display: flex;
}

.runtime{
    margin-left: 100px;
    font-family: 'Oswald', sans-serif;
    font-size: 35px;
}

.loader {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
  }

.title {
    display: inline;
    padding-left: 100px;
    padding-right: 30px;
    font-family: 'Oswald', sans-serif;
    font-size: 55px;
    color: #2c2c2c;
    text-decoration: none;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);

}

.movieimg {
    position:relative;
    top: -28px;
    left: 48px;
    max-width: 150px;
  width: 100%;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);

}

